//    
import axios from '../utils/request'

export const getSuggeListAPI = ({ keywords }) => axios({
  url: '/v1_0/suggestion',
  params: {
    q: keywords
  }
})

//    
export const getSearchResultAPI = ({ page = 1, per_page = 10, q  }) => axios({
  url: '/v1_0/search',
  params: {
    page,
    per_page,
    q
  }
})
