//  axios 
import ajax from 'axios'
import router from '../router'
import { Notify } from 'vant'
import { getToken, removeToken, setToken } from './token'
import { refreshTokenAPI } from '../api'
// axios
const axios = ajax.create({
  //baseURL: 'http://toutiao.itheima.net' // 
  baseURL: 'http://geek.itheima.net'
})

// 
axios.interceptors.request.use(function (config) {
  // 
  // : token
  // token, api/index.jsAuthorization, 
  // undefined, null
  // ?. , length, undefined
  // getToken()token, length
  if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
    config.headers.Authorization = `Bearer ${getToken()}`
  }
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(function (response) {
  // 2xx/3xx 
  return response
}, async function (error) {
  // 4xx/5xx  
  if (error.response.status === 401) {
    Notify({
      type: 'warning',
      message: ''
    })
    // token, , 
    // 1: , 
    // router.currentRoute  vuethis.$route -> 
    // fullPath, #
    removeToken()
    // router.currentRoute  vuethis.$route -> 
    // fullPath, #
    router.replace(`/login?path=${router.currentRoute.fullPath}`)

  //   // 2: refresh_tokentoken, JS, ()
  //   const res = await refreshTokenAPI()
  //   // token, 
  //   // 1. token
  //   setToken(res.data.data.token)
  //   // 2. token
  //   error.config.headers.Authorization = `Bearer ${res.data.data.token}`
  //   // 3. , 
  //   // error.config
  //   // return-returnPromise
  //   return axios(error.config)
  // } else if (error.response.status === 500 && error.config.url === '/v1_0/authorizations' && error.config.method === 'put') {
  //   // refresh_token
  //   localStorage.clear() // localStorage
  //   // localStorage, , localStorage
  //   Notify({
  //     type: 'warning',
  //     message: ''
  //   })
  //   router.replace('/login')
  }
  return Promise.reject(error)
})


// : token
// :
// 1. localStoragegeektoken()
// 2. /(token)
// 3. ,  401, 
//
// 401
// 1: token, , , token
// ,  -> 
// 2: token, refresh_token, , 
// token, ,  -> 
// 1. , localStorage.setItem('refresh_token', refresh_token)
// 2. 401, login, tokenapi
// 3. token
// 4. errorheaderstoken
// 5. axios, Promise
// : token, refresh_token500
//
// 401+500
// tokenrefresh_token (, 2token)
// 

// , 
export default ({
  url,
  method = 'GET',
  params,
  data,
  headers
}) => {
  return axios({
    url: url,
    method: method,
    params: params,
    data: data,
    headers: headers
  })
}




