//        
// offset, params(axiosnull)
import axios from '../utils/request'

export const getCommentListAPI = ({ id, offset = null, limit = 10 }) => axios({
  url: '/v1_0/comments',
  method: 'GET',
  params: { // axiosparams, , null, url?
    type: 'a', // : 
    source: id,
    offset,
    limit
  }
})

//  - 
export const commentLikeAPI = ({ comId }) => axios ({
  url: '/v1_0/comment/likings',
  method: 'POST',
  data: {
    target: comId
  }
})

// -
export const commentDisLikeAPI = ({ comId }) => axios({
  url: `/v1_0/comment/likings/${comId}`,
  method: 'DELETE'
})

//  - 
export const commentSendAPI = ({ artId, content }) => axios({
  url: '/v1_0/comments',
  method: 'POST',
  data: {
    target: artId,
    content: content
  }
})
