//    
import axios from '../utils/request'

export const userFollowAPI = ({ userId }) => axios({
  url: '/v1_0/user/followings',
  method: 'POST',
  data: {
    target: userId
  }
})

//    
export const userUnFollowAPI = ({ userId }) => axios({
  url: `/v1_0/user/followings/${userId}`,
  method: 'DELETE',
})

//  - ()
export const getUserProfileAPI = () => axios({
  url: '/v1_0/user/profile'
})

//  - 
export const getUserInfoAPI = () => axios({
  url: '/v1_0/user'
})

//  - 
// FormData, 
// Content-Type, axios, json
// Content-Type: application/json, , formDataContent-Type
// Content-Type: application/json; axios, : data -> json -> 
// Content-Type: multipart/form-data; , : dataFormData
export const updateUserAvatarAPI = (formData) => axios({
  url: '/v1_0/user/photo',
  method: 'PATCH',
  data: formData // fdnew FormData() 
})

//  - 
export const updateUserInfoAPI = (dataObj) => {
  const obj = {
    name: '',
    gender: 0,
    birthday: '',
    intro: ''
  }
  for (const prop in obj) { // key
    if (dataObj[prop] === undefined) { // key, ()
      delete obj[prop] // obj
    } else {
      obj[prop] = dataObj[prop] // , key, obj
    }
  }
  axios({
    url: '/v1_0/user/profile',
    method: 'PATCH', // ->PUT()
    // datanullkey+value, paramsnull
    data: obj
  })
}
